import React, { Component } from "react";
import IntlMessages from "../../../../components/utility/intlMessages";
import getLanguage from "../../../App/MyLanguange";
import { Form, Input, Col, Modal, Checkbox, Radio } from "antd";

const InputGroup = Input.Group;

class ModalAddReturnReason extends Component {
  state = {
    check_code: false
  };

  handleChangeCode = () => {
    const { setFieldsValue } = this.props.form;
    this.setState({ check_code: !this.state.check_code });
    if (this.state.check_code === false) {
      setFieldsValue({
        ["return_reason_code"]: null
      });
    }
  };

  componentDidUpdate = () => {
    const { resetFields } = this.props.form;
    const { success } = this.props.returnReason;
    if (success) {
      resetFields();
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { returnReason } = this.props;
    const loading = returnReason.loading;
    const error = returnReason.errorField ? returnReason.errorField : [];

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const radioGroupStyle = {
      display: "flex",
      width: "100%"
    };

    const radioButtonStyle = {
      flex: "1",
      textAlign: "center"
    };

    return (
      <Modal
        title={<IntlMessages id="returnReasons.add.title" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="40%"
        okText={getLanguage({ id: "button.submit" })}
        cancelText={getLanguage({ id: "button.cancel" })}
        okButtonProps={{ loading: loading }}
        cancelButtonProps={{ loading: loading }}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <div className="isoDetailDataFlex" style={{ padding: "0" }}>
            <InputGroup compact>
              <Col span={20}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="returnReasons.add.code" />}
                  validateStatus={
                    error.return_reason_code ||
                    getFieldError("return_reason_code")
                      ? "error"
                      : null
                  }
                  help={
                    error.return_reason_code
                      ? error.return_reason_code
                      : getFieldError("return_reason_code")
                  }
                >
                  {getFieldDecorator("return_reason_code", {
                    //initialValue: data.return_reason_code,
                    rules: [
                      {
                        max: 30,
                        message: <IntlMessages id="maxLength30" />
                      }
                    ]
                  })(<Input disabled={this.state.check_code ? true : false} />)}
                </Form.Item>
              </Col>
              <Col span={4}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="autoGenerateCode" />}
                >
                  <Checkbox
                    style={{
                      paddingTop: "5px",
                      paddingLeft: "8px"
                    }}
                    checked={this.state.check_code}
                    onChange={this.handleChangeCode}
                  />
                </Form.Item>
              </Col>
            </InputGroup>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="locations.add.description" />}
            >
              {getFieldDecorator("description", {
                //initialValue: data.description,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
              })(<Input />)}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="transactionType" />}
            >
              {getFieldDecorator("transaction_type", {
                //initialValue: data.transaction_type,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Radio.Group style={radioGroupStyle} buttonStyle="solid">
                  <Radio.Button style={radioButtonStyle} value={1}>
                    <IntlMessages id="All" />
                  </Radio.Button>
                  <Radio.Button
                    style={radioButtonStyle}
                    value={2}
                    disabled={true}
                  >
                    <IntlMessages id="Purchase" />
                  </Radio.Button>
                  <Radio.Button style={radioButtonStyle} value={3}>
                    <IntlMessages id="Sales" />
                  </Radio.Button>
                </Radio.Group>
              )}
            </Form.Item>
          </div>
        </Form>
      </Modal>
    );
  }
}

const WrappedModalAddReturnReason = Form.create({
  name: "modal_add_return_reason"
})(ModalAddReturnReason);

export default WrappedModalAddReturnReason;
