import React, { Component } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { compose } from "redux";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import {
  Form,
  Input,
  DatePicker,
  Select,
  Row,
  Col,
  Tooltip,
  Icon,
  Divider,
  Radio
} from "antd";
import EditableDeliveryOrder from "./editableDeliveryOrder";
import EditableSalesInvoice from "./editableSalesInvoice";
import ModalDeliveryOrder from "./modalDeliveryOrder";
import ModalSalesInvoice from "./modalSalesInvoice";
import ModalCash from "./modalCash";
import {
  setSaveData,
  getSaveData,
  removeSaveData
} from "../../../../helpers/helper";
import ModalAddReturnReason from "./modalAddReturnReason";
import getLanguage from "../../../App/MyLanguange";

const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;

/**
 * @param {String} discount diskon
 */
const formatDiscountMulti = discount => {
  if (discount.includes("+")) {
    const arr_discount = discount.split("+");

    let multi = 1;
    arr_discount.map(data => (multi *= 1 - parseFloat(data) / 100));

    return `${(1 - multi) * 100}%`;
  } else {
    return discount;
  }
};

class SalesReturnCreateForm extends Component {
  state = {
    id_location: null,
    id_customer: null,
    id_transaction: null,
    transaction_type: 1,
    transaction_no: null,
    item: null,
    items: [],
    stDetails: {
      sales_return_details: []
    },
    sales_invoice: null,
    cashDetails: {
      id_cash_bank: null,
      id_cash_flow_type: null,
      reference_no: null,
      bank_issuer: null,
      due_date: null
    },
    discount: "0",
    total_discount: 0,
    total_tax: 0,
    total_qty: 0,
    total_amount: 0,
    currency_rate: 1,
    inclusive_tax: 0,
    visibleDeliveryOrder: false,
    visibleSalesInvoice: false,
    visibleCash: false,
    err: false,
    err_item: false,
    restore: null,
    clear: false,
    cash_return: 0
  };

  componentDidMount = () => {
    /* Init Restore Autosave */
    let restore = getSaveData("st");
    if (restore) {
      restore.fields.return_date = moment();
      this.setState({ restore: restore });
    }
    /* END Init Restore Autosave */
  };

  componentDidUpdate = (prevProps, prevState) => {
    const {
      id_transaction,
      stDetails,
      cashDetails,
      transaction_type,
      transaction_no,
      sales_invoice,
      discount,
      total_discount,
      total_tax,
      total_qty,
      total_amount,
      currency_rate,
      inclusive_tax
      // cash_return
    } = this.state;
    const { getFieldsValue } = this.props.form;

    /* Restore Autosave */
    if (this.state.restore && prevProps.salesReturn.loadingForeign) {
      const { restore } = this.state;
      const { setFieldsValue } = this.props.form;
      setFieldsValue(restore.fields);
      // console.log(restore);
      this.setState({
        id_transaction: restore.id_transaction,
        discount: formatDiscountMulti(restore.discount),
        total_discount: restore.total_discount,
        total_tax: restore.total_tax,
        total_qty: restore.total_qty,
        total_amount: restore.total_amount,
        transaction_type: restore.fields.transaction_type,
        transaction_no: restore.transaction_no,
        currency_rate: restore.currency_rate,
        sales_invoice: restore.sales_invoice,
        // cashDetails: {
        //   id_cash_bank: restore.id_cash_bank,
        //   id_cash_flow_type: restore.id_cash_flow_type,
        //   reference_no: restore.reference_no,
        //   bank_issuer: restore.bank_issuer,
        //   due_date: restore.due_date
        // },
        // cash_return: restore.cash_return,
        inclusive_tax: restore.inclusive_tax,
        items: restore.sales_return_details,
        id_location: restore.fields.id_location,
        id_customer: restore.fields.id_customer,
        restore: null
      });
    }
    /* End Restore Autosave */

    /* Auto Save */
    let data = Object.assign(
      {},
      { fields: getFieldsValue() },
      stDetails,
      // cashDetails,
      {
        id_transaction: id_transaction,
        transaction_type: transaction_type,
        transaction_no: transaction_no,
        discount: formatDiscountMulti(discount),
        sales_invoice: sales_invoice,
        total_discount: total_discount,
        total_tax: total_tax,
        total_qty: total_qty,
        total_amount: total_amount,
        // cash_return: cash_return,
        currency_rate: currency_rate,
        inclusive_tax: inclusive_tax
      }
    );

    data.fields.return_date = data.fields.return_date
      ? data.fields.return_date.format("YYYY-MM-DD HH:mm:ss")
      : null;

    if (
      stDetails.sales_return_details !==
      prevState.stDetails.sales_return_details
    ) {
      setSaveData("st", data);
    }

    if (cashDetails !== prevState.cashDetails) {
      setSaveData("st", data);
    }

    /* END Autosave */
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      const {
        id_transaction,
        stDetails,
        discount,
        total_discount,
        total_tax,
        total_qty,
        total_amount,
        cashDetails,
        transaction_type,
        transaction_no,
        cash_return
      } = this.state;

      // const validateInvAmount = () =>
      //   parseFloat(total_amount) === parseFloat(values.customer_invoice_amount)
      //     ? false
      //     : true;

      // validasi item
      if (stDetails.sales_return_details.length <= 0) {
        return this.setState({ err_item: true });
      } else {
        this.setState({ err_item: false });
      }

      if (err) {
        this.setState({ err: true });
        return;
      }
      // else if (errInvAmount || validateInvAmount()) {
      //   this.setState({ err: true, errInvAmount: true });
      //   return;
      // }

      values.return_date = values.return_date.format("YYYY-MM-DD HH:mm:ss");

      const data = Object.assign({}, values, stDetails, cashDetails, {
        id_transaction: id_transaction,
        transaction_type: transaction_type,
        transaction_no: transaction_no,
        cash_return: cash_return,
        discount: formatDiscountMulti(discount),
        total_discount: total_discount,
        total_tax: total_tax,
        total_qty: total_qty,
        total_amount: total_amount
      });
      // console.log(data);
      if (!err) {
        this.setState({ err: false });
        this.props.submit(data);
      }
    });
  };

  handleModalCash = () => {
    const { visibleCash } = this.state;
    visibleCash
      ? this.setState({ visibleCash: false })
      : this.setState({ visibleCash: true });
  };

  handleModalDeliveryOrder = () => {
    const { visibleDeliveryOrder } = this.state;
    visibleDeliveryOrder
      ? this.setState({ visibleDeliveryOrder: false })
      : this.setState({ visibleDeliveryOrder: true });
  };

  handleModalSalesInvoice = () => {
    const { visibleSalesInvoice } = this.state;
    visibleSalesInvoice
      ? this.setState({ visibleSalesInvoice: false })
      : this.setState({ visibleSalesInvoice: true });
  };

  handleSelectLocation = id_location => {
    this.setState({ id_location: id_location, clear: true });
  };

  handleSubmitCash = value => {
    this.setState({
      cashDetails: {
        id_cash_bank: value.id_cash_bank,
        id_cash_flow_type: value.id_cash_flow_type,
        reference_no: value.reference_no,
        bank_issuer: value.bank_issuer,
        due_date: value.due_date
      },
      cash_return: value.cash_return
    });
    this.handleModalCash();
  };

  handleSelectModalDeliveryOrder = value => {
    const { deliveryOrders } = this.props.salesReturn;
    let deliveryOrder = deliveryOrders.find(
      curr_data => curr_data.id_delivery_order === value
    );

    let items = [...deliveryOrder.delivery_order_details];
    items.map((data, index) => (data.index_no = index + 1));

    this.setState({
      id_transaction: deliveryOrder.id_delivery_order,
      items: items,
      discount: "0",
      transaction_no: deliveryOrder.delivery_order_no,
      inclusive_tax: deliveryOrder.sales_order.inclusive_tax,
      clear: false
    });

    this.handleModalDeliveryOrder();
  };

  handleSelectModalSalesInvoice = value => {
    const { salesInvoices } = this.props.salesReturn;
    let salesInvoice = salesInvoices.find(
      curr_data => curr_data.id_sales_invoice === value
    );

    let sum = 0;
    salesInvoice.sales_invoice_details.map(
      item => (sum += parseFloat(item.sub_total))
    );
    salesInvoice.sum_sub_total = sum;

    let items = [...salesInvoice.sales_invoice_details];
    items.map((data, index) => (data.index_no = index + 1));

    this.setState({
      id_transaction: salesInvoice.id_sales_invoice,
      discount: formatDiscountMulti(salesInvoice.discount),
      items: items,
      inclusive_tax: salesInvoice.inclusive_tax,
      transaction_no: salesInvoice.sales_invoice_no,
      sales_invoice: salesInvoice,
      clear: false
    });

    this.handleModalSalesInvoice();
  };

  handleChangeCustomer = id_customer => {
    const { currencies, customers } = this.props.salesReturn;
    const customer = customers.find(data => data.id_customer === id_customer);
    const currency = currencies.find(
      data => data.id_currency === customer.id_currency
    );
    const currRate = currency
      ? currency.is_base
        ? 1
        : currency.currency_rate.rate
      : 1;
    this.setState({
      id_customer: id_customer,
      currency_rate: currRate,
      clear: true
    });
  };

  handleTransType = e => {
    this.setState({
      transaction_type: e.target.value,
      stDetails: { sales_return_details: [] },
      transaction_no: null,
      clear: true
    });
  };

  handleSaveItemList = (
    items,
    total_discount,
    total_tax,
    total_qty,
    total_amount
  ) => {
    this.setState({
      stDetails: { sales_return_details: items },
      total_discount: total_discount,
      total_tax: total_tax,
      total_qty: total_qty,
      total_amount: total_amount,
      clear: false
    });
  };

  clearItems = (items, total_discount, total_tax, total_qty, total_amount) => {
    this.setState({
      items: [],
      stDetails: { sales_return_details: items },
      total_discount: total_discount,
      total_tax: total_tax,
      total_qty: total_qty,
      total_amount: total_amount,
      clear: false
    });
  };

  render() {
    // console.log(this.state.stDetails);
    const {
      visibleDeliveryOrder,
      visibleSalesInvoice,
      visibleCash
    } = this.state;
    const { getFieldDecorator, getFieldValue } = this.props.form;

    const { salesReturn } = this.props;
    const {
      locations,
      customers,
      returnReasons,
      cashBanks,
      cashFlowTypes
    } = salesReturn ? salesReturn : [];
    const { user } = this.props;
    let { permissions, default_employee_location } = user ? user : [];
    permissions = permissions ? permissions : [];
    default_employee_location = default_employee_location
      ? default_employee_location
      : [];
    const {
      loading,
      loadingSubmit,
      loadingItems,
      loadingDeliveryOrders,
      loadingSalesInvoices,
      loadingForeign
    } = salesReturn ? salesReturn : [];
    // const data = salesReturn.salesReturn ? salesReturn.salesReturn : [];

    // const used = data.is_used;
    const error = salesReturn
      ? salesReturn.errorField
        ? salesReturn.errorField
        : []
      : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <>
        <ModalAddReturnReason
          handleModal={this.props.handleModalAddReturnReason}
          returnReason={this.props.returnReason}
          submit={this.props.handleSubmitReturnReason}
          visible={this.props.visibleAddReturnReason}
        />
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <ModalDeliveryOrder
            visible={visibleDeliveryOrder}
            salesReturn={salesReturn}
            handleModal={this.handleModalDeliveryOrder}
            handleSelectModal={this.handleSelectModalDeliveryOrder}
            id_location={getFieldValue("id_location")}
            id_customer={getFieldValue("id_customer")}
          />

          <ModalSalesInvoice
            visible={visibleSalesInvoice}
            salesReturn={salesReturn}
            handleModal={this.handleModalSalesInvoice}
            handleSelectModal={this.handleSelectModalSalesInvoice}
            id_location={getFieldValue("id_location")}
            id_customer={getFieldValue("id_customer")}
          />

          <ModalCash
            visible={visibleCash}
            transaction_status={1}
            handleModal={this.handleModalCash}
            id_location={getFieldValue("id_location")}
            submit={this.handleSubmitCash}
            salesReturn={{ salesReturn: this.state.cashDetails }}
            cashBanks={cashBanks}
            cashFlowTypes={cashFlowTypes}
            transDate={getFieldValue("return_date")}
          />

          <Row>
            <Col span={12} style={{ paddingRight: "15px" }}>
              <InputGroup>
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="salesReturns.salesReturnNo" />}
                  >
                    {getFieldDecorator("sales_return_no", {
                      // initialValue: data.sales_return_no,
                    })(<Input disabled={true} />)}
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="salesReturns.transactionDate" />}
                    style={{ marginBottom: 0, paddingBottom: 0 }}
                  >
                    {getFieldDecorator("return_date", {
                      initialValue: moment()
                    })(
                      <DatePicker
                        style={{ width: "100%" }}
                        showTime
                        format="DD MMMM YYYY HH:mm:ss"
                        disabled={true}
                        allowClear={false}
                        disabledDate={e => e.valueOf() > moment()}
                      />
                    )}
                  </Form.Item>
                </Col>
              </InputGroup>

              <Col span={24}>
                <InputGroup>
                  <Col span={16}>
                    <Form.Item
                      {...formItemLayout}
                      label={
                        <Tooltip
                          placement="top"
                          title={
                            <IntlMessages id="salesReturns.location.help" />
                          }
                        >
                          <IntlMessages id="salesReturns.location" />
                          <Icon
                            style={{ marginLeft: "5px" }}
                            type="question-circle"
                            theme="filled"
                          />
                        </Tooltip>
                      }
                    >
                      {getFieldDecorator("id_location", {
                        initialValue: default_employee_location.id_location,
                        rules: [
                          {
                            required: true,
                            message: <IntlMessages id="required" />
                          }
                        ]
                      })(
                        <Select
                          showSearch
                          style={{ width: "100%" }}
                          placeholder={
                            <IntlMessages id="salesReturns.location" />
                          }
                          loading={loadingForeign}
                          onSelect={this.handleSelectLocation}
                          optionFilterProp="children"
                          filterOption={true}
                        >
                          {locations.map((data, key) => (
                            <Option key={key} value={data.id_location}>
                              {`${data.location_code} (${data.location_name})`}
                            </Option>
                          ))}
                        </Select>
                      )}
                    </Form.Item>
                  </Col>

                  <Col span={8}>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="salesReturns.returnFrom" />}
                    >
                      {getFieldDecorator("transaction_type", {
                        initialValue: 1
                      })(
                        <Radio.Group
                          onChange={this.handleTransType}
                          // value={this.state.inclusive_tax}
                          buttonStyle="solid"
                          style={{ width: "100%" }}
                        >
                          <Tooltip
                            title={<IntlMessages id="sidebar.deliveryOrder" />}
                            placement="bottom"
                          >
                            <Radio.Button
                              style={{ width: "50%", textAlign: "center" }}
                              value={1}
                            >
                              DO
                            </Radio.Button>
                          </Tooltip>
                          <Tooltip
                            title={
                              <IntlMessages id="salesInvoices.pageHeader" />
                            }
                            placement="bottom"
                          >
                            <Radio.Button
                              style={{ width: "50%", textAlign: "center" }}
                              value={2}
                            >
                              SI
                            </Radio.Button>
                          </Tooltip>
                        </Radio.Group>
                      )}
                    </Form.Item>
                  </Col>
                </InputGroup>
              </Col>
            </Col>
            <Col span={12} style={{ paddingLeft: "15px" }}>
              <Form.Item
                {...formTextArea}
                label={<IntlMessages id="salesReturns.note" />}
              >
                {getFieldDecorator("note", {
                  // initialValue: data.note
                })(<TextArea autosize={{ minRows: 4 }} />)}
              </Form.Item>
            </Col>
          </Row>
          <Row>
            <Col span={12} style={{ paddingRight: "15px" }}>
              <InputGroup>
                <Col span={16}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="salesReturns.customer" />}
                  >
                    {getFieldDecorator("id_customer", {
                      // initialValue: data.id_customer,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Select
                        showSearch
                        style={{ width: "100%" }}
                        placeholder={
                          <IntlMessages id="salesReturns.customer" />
                        }
                        loading={loadingForeign}
                        onSelect={this.handleChangeCustomer}
                        optionFilterProp="children"
                        filterOption={true}
                      >
                        {customers.map((data, key) => (
                          <Option key={key} value={data.id_customer}>
                            {`${data.customer_code} (${data.customer_name})`}
                          </Option>
                        ))}
                      </Select>
                    )}
                  </Form.Item>
                </Col>
                <Col span={8}>
                  <Form.Item
                    {...formItemLayout}
                    label={
                      <Tooltip
                        placement="top"
                        title={
                          <IntlMessages id="salesReturns.importTransaction.help" />
                        }
                      >
                        <IntlMessages id="salesReturns.importTransaction" />
                        <Icon
                          style={{ marginLeft: "5px" }}
                          type="question-circle"
                          theme="filled"
                        />
                      </Tooltip>
                    }
                  >
                    {this.state.transaction_type === 1 ? (
                      <Button
                        icon="select"
                        disabled={
                          loadingDeliveryOrders ||
                          !getFieldValue("id_location") ||
                          !getFieldValue("id_customer")
                        }
                        onClick={this.handleModalDeliveryOrder}
                        style={{ height: "31px", width: "100%" }}
                        // shape="circle"
                      >
                        <IntlMessages id="salesReturns.buttonImportDO" />
                      </Button>
                    ) : (
                      <Button
                        icon="select"
                        disabled={
                          loadingSalesInvoices ||
                          !getFieldValue("id_location") ||
                          !getFieldValue("id_customer")
                        }
                        onClick={this.handleModalSalesInvoice}
                        style={{ height: "31px", width: "100%" }}
                        // shape="circle"
                      >
                        <IntlMessages id="salesReturns.buttonImportSI" />
                      </Button>
                    )}
                  </Form.Item>
                </Col>
              </InputGroup>
            </Col>
            <Col span={12} style={{ paddingLeft: "15px" }}>
              <InputGroup>
                <Col span={12}>
                  {getFieldValue("transaction_type") === 1 ? (
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="deliveryOrders.no" />}
                    >
                      <Input
                        value={this.state.transaction_no}
                        disabled={true}
                      />
                    </Form.Item>
                  ) : (
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="salesInvoice.no" />}
                    >
                      <Input
                        value={this.state.transaction_no}
                        disabled={true}
                      />
                    </Form.Item>
                  )}
                </Col>
                {/* <Col span={8}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <Tooltip
                      placement="top"
                      title={<IntlMessages id="salesReturns.setCash.help" />}
                    >
                      <IntlMessages id="salesReturns.setCash" />
                      <Icon
                        style={{ marginLeft: "5px" }}
                        type="question-circle"
                        theme="filled"
                      />
                    </Tooltip>
                  }
                >
                  <Button
                    type={this.state.cash_return ? "primary" : "default"}
                    icon="plus"
                    disabled={
                      loadingSalesInvoices ||
                      loadingDeliveryOrders ||
                      !getFieldValue("id_location")
                    }
                    onClick={this.handleModalCash}
                    style={{ height: "31px", width: "100%" }}
                    // shape="circle"
                  >
                    <IntlMessages id="salesReturns.setCash.button" />
                  </Button>
                </Form.Item>
              </Col> */}
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={
                      <Tooltip
                        placement="top"
                        title={
                          <IntlMessages id="salesReturns.returnReason.help" />
                        }
                      >
                        <IntlMessages id="salesReturns.returnReason" />
                        <Icon
                          style={{ marginLeft: "5px" }}
                          type="question-circle"
                          theme="filled"
                        />
                      </Tooltip>
                    }
                  >
                    {getFieldDecorator("id_return_reason", {
                      // initialValue: data.id_return_reason,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Select
                        showSearch
                        style={{ width: "100%" }}
                        placeholder={
                          <IntlMessages id="salesReturns.returnReason" />
                        }
                        loading={loadingForeign}
                        optionFilterProp="children"
                        filterOption={true}
                      >
                        {permissions.includes("Create Return Reason") ? (
                          <Option
                            key={"Create Return Reason"}
                            value={undefined}
                            onClick={() =>
                              this.props.handleModalAddReturnReason()
                            }
                          >
                            <div
                              style={{ color: "#1890FF", fontStyle: "italic" }}
                            >
                              {" + " +
                                getLanguage({
                                  id: "returnReasons.pageHeader"
                                })}
                            </div>
                          </Option>
                        ) : null}

                        {returnReasons.map((data, key) => (
                          <Option key={key} value={data.id_return_reason}>
                            {`${data.return_reason_code} (${data.description})`}
                          </Option>
                        ))}
                      </Select>
                    )}
                  </Form.Item>
                </Col>
              </InputGroup>
            </Col>
          </Row>
          {/* </TabPane> */}
          {/* <TabPane
            tab={<IntlMessages id="salesReturns.tabs.payment" />}
            key={2}
          > */}
          {/* </TabPane>
        </Tabs> */}

          {this.state.transaction_type === 1 ? (
            <div>
              <Divider orientation="left" style={{ fontSize: 14 }}>
                <IntlMessages id="salesReturns.subtitle.DOitems" />
              </Divider>

              <Row style={{ marginBottom: "40px" }}>
                <Col span={24}>
                  <EditableDeliveryOrder
                    items={this.state.items}
                    inclusive_tax={this.state.inclusive_tax}
                    status={1}
                    handleSaveItemList={this.handleSaveItemList.bind(this)}
                    clearItems={this.clearItems}
                    loadingTransactions={loadingDeliveryOrders}
                    loadingItems={loadingItems}
                    clear={this.state.clear}
                  />
                </Col>
              </Row>
            </div>
          ) : (
            <div>
              <Divider orientation="left" style={{ fontSize: 14 }}>
                <IntlMessages id="salesReturns.subtitle.SIitems" />
              </Divider>

              <Row style={{ marginBottom: "40px" }}>
                <Col span={24}>
                  <EditableSalesInvoice
                    items={this.state.items}
                    sales_invoice={this.state.sales_invoice}
                    discount={this.state.discount}
                    inclusive_tax={this.state.inclusive_tax}
                    status={1}
                    handleSaveItemList={this.handleSaveItemList.bind(this)}
                    clearItems={this.clearItems}
                    loadingTransactions={loadingSalesInvoices}
                    loadingItems={loadingItems}
                    clear={this.state.clear}
                  />
                </Col>
              </Row>
            </div>
          )}

          <Form.Item {...tailFormItemLayout}>
            <Button
              type="primary"
              htmlType="submit"
              style={{ marginTop: "24px" }}
              loading={loading || loadingSubmit}
              disabled={!permissions.includes("Create Sales Return")}
              onClick={() => removeSaveData("st")}
            >
              <IntlMessages id="button.submit" />
            </Button>

            <Row>
              <Col span={24}>
                {this.state.err_item ? (
                  <IntlMessages id="itemDataEmpty">
                    {txt => <span style={{ color: "#f5222d" }}>{txt}</span>}
                  </IntlMessages>
                ) : null}
              </Col>
            </Row>

            <br />
            {this.state.err ? (
              <span style={{ color: "#f5222d" }}>
                <IntlMessages id="form.error" />
              </span>
            ) : null}
          </Form.Item>
        </Form>
      </>
    );
  }
}

const WrappedSalesReturnCreateForm = Form.create({
  name: "create_sales_return"
})(SalesReturnCreateForm);

const mapStateToProps = state => {
  return {
    user: state.auth.user,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps))(WrappedSalesReturnCreateForm);
