import React, { Component } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { compose } from "redux";
import actions from "../../../../redux/sales/salesReturn/actions";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";

import {
  Modal,
  Tooltip,
  Table,
  Collapse,
  Select,
  DatePicker,
  Input,
  Row,
  Col,
  Empty
} from "antd";
import {
  StatusTag,
  StatusBadge,
  TransNo,
  customCollapseStyle
} from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";

const Panel = Collapse.Panel;
const Option = Select.Option;
const { RangePicker } = DatePicker;
const Search = Input.Search;
const { getDeliveryOrders } = actions;

const expandedRowRender = data => {
  const columns = [
    {
      title: getLanguage({ id: "no." }),
      dataIndex: "index_no",
      width: "5%"
    },
    {
      title: getLanguage({ id: "code" }),
      dataIndex: "item_code",
      width: "15%",
      render: text => {
        if (text && text.length > 25) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 25) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: getLanguage({ id: "itemName" }),
      dataIndex: "item_name",
      width: "30%",
      render: text => {
        if (text && text.length > 50) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 50) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: (
        <Tooltip placement="top" title={<IntlMessages id="SOQty" />}>
          <a href="javascript:;"><IntlMessages id="SOQtys" /></a>
        </Tooltip>
      ),
      dataIndex: "ordered_qty", // dari PR
      width: "10%",
      align: "center",
      render: text => <NumberFormat value={text} />
    },
    {
      title: (
        <Tooltip placement="top" title={<IntlMessages id="deliveredQuantity" />}>
          <a href="javascript:;"><IntlMessages id="deliveredQty" /></a>
        </Tooltip>
      ),
      dataIndex: "sales_order_detail.delivered_qty", // dari PO
      width: "10%",
      align: "center",
      render: text => <NumberFormat value={text} />
    },
    {
      title: (
        <Tooltip placement="top" title={<IntlMessages id="leftQuantity" />}>
          <a href="javascript:;"><IntlMessages id="leftQty" /></a>
        </Tooltip>
      ),
      dataIndex: "left_qty", // dari PR
      width: "10%",
      align: "right",
      render: (text, row) => {
        const qty = parseFloat(row.ordered_qty);
        const delivered_qty = parseFloat(row.sales_order_detail.delivered_qty);

        return (<NumberFormat value={qty - delivered_qty} />);
      }
    },
    {
      title: (
        <Tooltip placement="top" title={<IntlMessages id="deliveryQuantity" />}>
          <a href="javascript:;"><IntlMessages id="deliveryQty" /></a>
        </Tooltip>
      ),
      dataIndex: "delivery_qty", // dari PR
      width: "10%",
      align: "center",
      render: (text, row) => <><NumberFormat value={text} /> {row.unit_code}</>
    },
    {
      title: (
        <Tooltip placement="top" title={<IntlMessages id="bsQuantity" />}>
          <a href="javascript:;"><IntlMessages id="bsQty" /></a>
        </Tooltip>
      ),
      dataIndex: "base_qty", // dari PR
      width: "10%",
      align: "center",
      render: (text, row) => <><NumberFormat value={text} /> {row.base_unit_code}</>
    }
  ];

  const dataSource = data;
  return (
    <div>
      <strong>
        <IntlMessages id="purchaseInvoices.importPR.itemList" />
      </strong>

      <Table
        rowKey={record => record.index_no}
        size="small"
        columns={columns}
        dataSource={dataSource}
        pagination={true}
        style={{ marginTop: "24px" }}
      />
    </div>
  );
};

class ModalDeliveryOrder extends Component {
  state = {
    filter: {
      id_customer: this.props.id_customer,
      id_location: this.props.id_location,
      id_salesman: null,
      status: 2,
      start_date: null,
      end_date: null,
      pagination: null
    },
    search: null
  };

  componentDidUpdate(prevProps) {
    const { id_location, id_customer } = this.props;

    if (
      prevProps.id_location !== id_location &&
      prevProps.id_customer !== id_customer &&
      id_location &&
      id_customer
    ) {
      this.handleLocationCustomerChange(id_location, id_customer);
      this.setState({
        filter: {
          ...this.state.filter,
          id_customer: id_customer,
          id_location: id_location
        }
      });
    } else if (prevProps.id_location !== id_location && id_location && id_customer) {
      this.handleLocationCustomerChange(id_location, id_customer);
      this.setState({
        filter: {
          ...this.state.filter,
          id_customer: id_customer,
          id_location: id_location
        }
      });
    } else if (prevProps.id_customer !== id_customer && id_customer && id_location) {
      this.handleLocationCustomerChange(id_location, id_customer);
      this.setState({
        filter: {
          ...this.state.filter,
          id_customer: id_customer,
          id_location: id_location
        }
      });
    }
  }

  handleLocationCustomerChange = (id_location, id_customer) => {
    const { getDeliveryOrders } = this.props;
    const string = "id_location=" + id_location + "&id_customer=" + id_customer;

    getDeliveryOrders(1, null, string);
  };

  handleLocationChange = id_location => {
    const { getDeliveryOrders } = this.props;
    const { id_customer } = this.state.filter;
    let string = "id_location=" + id_location;
    if (id_customer) {
      string += "&id_customer=" + id_customer;
    }
    getDeliveryOrders(1, null, string);
  };

  handleCustomerChange = id_customer => {
    const { getDeliveryOrders } = this.props;
    const { id_location } = this.state.filter;
    let string = "id_customer=" + id_customer;
    if (id_location) {
      string += "&id_location=" + id_location;
    }
    getDeliveryOrders(1, null, string);
  };

  handleSelect = value => {
    const { handleSelectModal } = this.props;
    handleSelectModal(value);
  };

  // Handle search
  handleSearch = value => {
    const { getDeliveryOrders } = this.props;
    getDeliveryOrders(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    this.setState({
      filter: {
        id_customer: this.props.id_customer,
        id_location: this.props.id_location,
        id_salesman: null,
        status: 2,
        start_date: null,
        end_date: null,
        pagination: null
      },
      search: null
    }, () => this.handleFilter());
  };

  //Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  // Handle filter
  handleFilter = () => {
    const { getDeliveryOrders } = this.props;
    //const { current_page_pr } = this.props.customer;
    const { filter } = this.state;
    let string = "";

    if (filter.id_location) {
      string += "id_location=" + filter.id_location;
    }
    // if (filter.status) {
    //   string
    //     ? (string += "&status=" + filter.status)
    //     : (string += "status=" + filter.status);
    // }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }
    if (filter.id_customer) {
      string
        ? (string += "&id_customer=" + filter.id_customer)
        : (string += "id_customer=" + filter.id_customer);
    }
    if (filter.id_salesman) {
      string
        ? (string += "&id_salesman=" + filter.id_salesman)
        : (string += "id_salesman=" + filter.id_salesman);
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }
    // console.log(string);
    this.setState({ search: null });
    getDeliveryOrders(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    this.setState({
      filter: {
        id_customer: this.props.id_customer,
        id_location: this.props.id_location,
        id_salesman: null,
        status: 2,
        start_date: null,
        end_date: null,
        pagination: null
      },
      search: null
    }, () => this.handleFilter());
  };

  // Handle pagination
  handlePageChange = page => {
    const { getDeliveryOrders } = this.props;
    const { search, filter } = this.props.salesReturn;
    getDeliveryOrders(page, search, filter);
  };

  render() {
    // console.log(this.props);
    const { visible, handleModal, salesReturn } = this.props;
    const { deliveryOrders, salesman, locations, customers } = salesReturn
      ? salesReturn
      : [];
    const { loadingForeign, loadingDeliveryOrders } = salesReturn
      ? salesReturn
      : false;
    
    const columns = [
      {
        title: getLanguage({ id: "deliveryOrderNo" }),
        dataIndex: "delivery_order_no",
        rowKey: "delivery_order_no",
        width: "25%",
        render: (text, row) => {
          let className;
          switch (row.status) {
            case 1:
              className = "new";
              break;
            case 2:
              className = "approved";
              break;
            case 3:
              className = "rejected";
              break;
            case 4:
              className = "invoiced";
              break;
            case 5:
              className = "cancelled";
              break;
            default:
              className = "new";
              break;
          }
  
          return (
            <div style={{ display: "flex", height: "42px" }}>
              <StatusBadge className={className} />
              <Tooltip
                placement="bottom"
                title={<IntlMessages id="tooltip.select" />}
              >
                <a
                  onClick={e => {
                    e.preventDefault();
                    this.handleSelect(row.id_delivery_order);
                  }}
                  href="javascript:;"
                >
                  <TransNo style={{ width: "14px" }}>{text}</TransNo>
                </a>
              </Tooltip>
            </div>
          );
          // return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "salesOrders.salesOrderNo" }),
        dataIndex: "sales_order_no",
        rowKey: "sales_order_no",
        width: "25%"
      },
      {
        title: getLanguage({ id: "date" }),
        dataIndex: "delivery_order_date",
        rowKey: "delivery_order_date",
        width: "10%",
        render: text => (
          <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
        )
      },
      {
        title: getLanguage({ id: "sidebar.customer" }),
        dataIndex: "customer_name",
        rowKey: "customer_name",
        width: "15%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location_name",
        rowKey: "location_name",
        width: "15%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "createdBy" }),
        dataIndex: "created_by",
        rowKey: "created_by",
        width: "10%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "status",
        rowKey: "status",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          switch (text) {
            case 1:
              className = "new";
              display = getLanguage({ id: "new" });
              break;
            case 2:
              className = "approved";
              display = getLanguage({ id: "approved" });
              break;
            case 3:
              className = "rejected";
              display = getLanguage({ id: "rejected" });
              break;
            case 4:
              className = "invoiced";
              display = getLanguage({ id: "invoiced" });
              break;
            case 5:
              className = "cancelled";
              display = getLanguage({ id: "cancelled" });
              break;
            default:
              className = "new";
              display = getLanguage({ id: "new" });
              break;
          }
  
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];

    const dataSource = [];
    Object.keys(deliveryOrders).map((item, index) => {
      return dataSource.push({
        ...deliveryOrders[item],
        key: item
      });
    });

    const { current_page_pr, page_size_pr, total_pr } = this.props.salesReturn
      ? this.props.salesReturn
      : [];

    return (
      <Modal
        title={<IntlMessages id="salesInvoices.importDO" />}
        visible={visible}
        centered
        footer={null}
        keyboard={true}
        onCancel={handleModal}
        width="95%"
        okText={getLanguage({ id: "button.ok" })}
        cancelText={getLanguage({ id: "button.cancel" })}
      >
        <Row style={{ marginBottom: "20px" }} type="flex">
          <Col span={18}>
            <Search
              placeholder={
                `${getLanguage({ id: "deliveryOrderNo" })}, ` +
                `${getLanguage({ id: "createdBy" })}`
              }
              onSearch={this.handleSearch}
              style={{ width: "99%", height: "36px" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
          </Col>
          <Col span={6}>
            <Button
              type="primary"
              onClick={this.handleSearch.bind(this, this.state.search)}
              style={{ marginRight: "2%" }}
            >
              <IntlMessages id="button.search" />
            </Button>

            <Button onClick={this.handleResetSearch}>
              <IntlMessages id="filter.reset" />
            </Button>
          </Col>
        </Row>
        <Row style={{ marginBottom: "20px" }}>
          <Col>
            <Collapse style={customCollapseStyle}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "12px" }}
                >
                  <Input.Group>
                    <Col span={12}>
                      <label>
                        <IntlMessages id="customers.pageHeader" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "id_customer"
                        )}
                        value={
                          this.state.filter.id_customer
                            ? this.state.filter.id_customer
                            : []
                        }
                        style={{ width: "100%" }}
                        placeholder={
                          <IntlMessages id="deliveryOrders.filter.select.customer" />
                        }
                        optionFilterProp="children"
                        filterOption={true}
                        loading={loadingForeign}
                        allowClear={true}
                        disabled={true}
                        //showArrow={false}
                      >
                        {customers.map((data, key) => (
                          <Option key={key} value={data.id_customer}>
                            {`${data.customer_code} (${data.customer_name})`}
                          </Option>
                        ))}
                      </Select>
                    </Col>
                    <Col span={12}>
                      <label>
                        <IntlMessages id="locations.pageHeader" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "id_location"
                        )}
                        value={
                          this.state.filter.id_location
                            ? this.state.filter.id_location
                            : []
                        }
                        style={{ width: "100%" }}
                        placeholder={
                          <IntlMessages id="deliveryOrders.filter.select.location" />
                        }
                        optionFilterProp="children"
                        filterOption={true}
                        loading={loadingForeign}
                        allowClear={true}
                        disabled={true}
                        //showArrow={false}
                      >
                        {locations.map((data, key) => (
                          <Option key={key} value={data.id_location}>
                            {`${data.location_code} (${data.location_name})`}
                          </Option>
                        ))}
                      </Select>
                    </Col>
                  </Input.Group>
                </Row>
                <Row
                  type="flex"
                  justify="start"
                  style={{ marginBottom: "12px" }}
                >
                  <Input.Group>
                    <Col span={12}>
                      <label>
                        <IntlMessages id="purchaseOrders.transactionDate" />
                      </label>
                      <br />
                      <RangePicker
                        format="DD MMMM YYYY HH:mm:ss"
                        showTime
                        allowClear={true}
                        style={{ width: "100%" }}
                        value={
                          this.state.filter.start_date &&
                          this.state.filter.end_date
                            ? [
                                moment(this.state.filter.start_date),
                                moment(this.state.filter.end_date)
                              ]
                            : []
                        }
                        onChange={this.handleFilterChange.bind(this, "date")}
                      />
                    </Col>
                    <Col span={12}>
                      <label>
                        <IntlMessages id="salesman" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "id_salesman"
                        )}
                        value={
                          this.state.filter.id_salesman
                            ? this.state.filter.id_salesman
                            : []
                        }
                        style={{ width: "100%" }}
                        placeholder={<IntlMessages id="salesman.select" />}
                        optionFilterProp="children"
                        filterOption={true}
                        loading={loadingForeign}
                        allowClear={true}
                        //showArrow={false}
                      >
                        {salesman.map((data, key) => (
                          <Option key={key} value={data.id_employee}>
                            {`${data.employee_code} (${data.first_name} ${
                              data.last_name
                            })`}
                          </Option>
                        ))}
                      </Select>
                    </Col>
                  </Input.Group>
                </Row>
                <Row type="flex" justify="start">
                  <Col span={3}>
                    <label>
                      <IntlMessages id="dataPerPage" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "pagination"
                      )}
                      value={
                        this.state.filter.pagination
                          ? this.state.filter.pagination
                          : page_size_pr
                      }
                      style={{ width: "80%" }}
                      // placeholder={
                      //   <IntlMessages id="pettyCashTypes.filter.select.transactionType" />
                      // }
                      optionFilterProp="children"
                      filterOption={true}
                      //allowClear={true}
                      //showArrow={false}
                    >
                      <Option key={1} value={10}>
                        10
                      </Option>
                      <Option key={2} value={25}>
                        25
                      </Option>
                      <Option key={3} value={50}>
                        50
                      </Option>
                      <Option key={4} value={100}>
                        100
                      </Option>
                    </Select>
                  </Col>

                  <Col
                    span={6}
                    style={{
                      marginTop: "auto"
                    }}
                  >
                    <Button
                      size="small"
                      type="primary"
                      style={{ marginRight: "10px" }}
                      onClick={this.handleFilter}
                    >
                      <IntlMessages id="filter.submit" />
                    </Button>
                    <Button onClick={this.handleResetFilter} size="small">
                      <IntlMessages id="filter.reset" />
                    </Button>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </Col>
        </Row>
        <Row>
          <Col>
            {dataSource.length === 0 && !loadingDeliveryOrders ? (
              <Empty
                description={<IntlMessages id="deliveryOrders.noData" />}
              />
            ) : (
              <Table
                dataSource={dataSource}
                columns={columns}
                loading={loadingDeliveryOrders}
                expandedRowRender={record =>
                  expandedRowRender(record.delivery_order_details)
                }
                expandIcon={props => (
                  <Button
                    onClick={e => props.onExpand(props.record, e)}
                    size="small"
                  >
                    <IntlMessages id="button.view" />
                  </Button>
                )}
                // Set pagination, jika ada page_size_pr mengunakan pagination API
                // jika tidak ada page_size_pr menggunakan pagination bawaan
                pagination={
                  page_size_pr
                    ? {
                        current: current_page_pr,
                        pageSize: parseInt(page_size_pr),
                        total: total_pr,
                        onChange: this.handlePageChange.bind(this)
                      }
                    : true
                }
              />
            )}
          </Col>
        </Row>
      </Modal>
    );
  }
}

export default compose(
  connect(
    null,
    { getDeliveryOrders }
  )
)(ModalDeliveryOrder);
