const actions = {
  getSalesReturns: (page, search, filter) => ({
    type: "GET_SALES_RETURNS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createSalesReturn: () => ({
    type: "CREATE_SALES_RETURN_REQUEST"
  }),
  getDeliveryOrders: (page, search, filter) => ({
    type: "GET_FILTER_DELIVERY_ORDERS_ST_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  getSalesInvoices: (page, search, filter) => ({
    type: "GET_FILTER_SALES_INVOICES_PT_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  storeSalesReturn: data => ({
    type: "STORE_SALES_RETURN_REQUEST",
    payload: { body: data }
  }),
  editSalesReturn: (id_sales_return, search) => ({
    type: "EDIT_SALES_RETURN_REQUEST",
    payload: { id_sales_return: id_sales_return, search: search }
  }),
  updateSalesReturn: (id_sales_return, data) => ({
    type: "UPDATE_SALES_RETURN_REQUEST",
    payload: {
      id_sales_return: id_sales_return,
      body: data
    }
  }),
  showSalesReturn: id_sales_return => ({
    type: "SHOW_SALES_RETURN_REQUEST",
    payload: { id_sales_return: id_sales_return }
  }),
  confirmSalesReturn: id_sales_return => ({
    type: "CONFIRM_SALES_RETURN_REQUEST",
    payload: { id_sales_return }
  }),
  unconfirmSalesReturn: id_sales_return => ({
    type: "UNCONFIRM_SALES_RETURN_REQUEST",
    payload: { id_sales_return }
  }),
  closeSalesReturn: id_sales_return => ({
    type: "CLOSE_SALES_RETURN_REQUEST",
    payload: { id_sales_return }
  }),
  cancelSalesReturn: id_sales_return => ({
    type: "CANCEL_SALES_RETURN_REQUEST",
    payload: { id_sales_return }
  }),
  uploadDocument: (id_sales_return, data) => ({
    type: "UPLOAD_SALES_RETURN_DOCUMENT_REQUEST",
    payload: {
      id_sales_return,
      body: data
    }
  }),
  downloadDocument: id_sales_return_document => ({
    type: "DOWNLOAD_SALES_RETURN_DOCUMENT_REQUEST",
    payload: { id_sales_return_document }
  }),
  deleteDocument: (id_sales_return_document, id_sales_return) => ({
    type: "DELETE_SALES_RETURN_DOCUMENT_REQUEST",
    payload: {
      id_sales_return_document: id_sales_return_document,
      id_sales_return: id_sales_return
    }
  }),
  setPrintData: data => ({
    type: "SET_PRINT_DATA_SALES_RETURN",
    payload: { body: data }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
