import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import SalesReturnCreateForm from "./createForm";
import notification from "../../../../components/notification";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/sales/salesReturn/actions";
import returnReasonActions from "../../../../redux/item/returnReason/actions";
import { Box } from "../../../App/app.style";
import { removeSaveData } from "../../../../helpers/helper";
import { Tabs } from "antd";

const redirecTo = "/dashboard/sales/sales-returns";
const TabPane = Tabs.TabPane;
const { storeReturnReason } = returnReasonActions;

class CreateSalesReturn extends Component {
  state = {
    visibleAddReturnReason: false
  };

  componentDidMount() {
    const { createSalesReturn } = this.props;
    createSalesReturn();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.salesReturn.error) {
      notification("error", this.props.salesReturn.error);
      this.props.clearError();
    }
    if (
      (this.props.salesReturn.success || this.props.salesReturn.warning) &&
      this.props.salesReturn.id_sales_return
    ) {
      this.props.history.replace(
        redirecTo + "/update/" + this.props.salesReturn.id_sales_return
      );
      // this.props.history.replace(redirecTo);
      // notification("success", this.props.salesReturn.success);
      // this.props.clearSuccess();
    }

    // Return Reason
    if (this.props.returnReason.error) {
      notification("error", this.props.returnReason.error);
      this.props.clearError();
    }

    if (this.props.returnReason.success) {
      const { createSalesReturn, clearSuccess } = this.props;
      notification("success", this.props.returnReason.success);
      this.handleModalAddReturnReason();
      clearSuccess();
      createSalesReturn();
    }
  }

  handleModalAddReturnReason = () => {
    this.setState({
      visibleAddReturnReason: !this.state.visibleAddReturnReason
    });
  };

  handleSubmit = value => {
    const { storeSalesReturn } = this.props;
    storeSalesReturn(value);
  };

  handleSubmitReturnReason = value => {
    const { storeReturnReason } = this.props;
    storeReturnReason(value);
  };

  render() {
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="salesReturns.button.add" />}>
            <Button
              onClick={() => {
                removeSaveData("st");
                this.props.history.replace(redirecTo);
              }}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="jobCostings.tab.detail" />} key="1">
              <SalesReturnCreateForm
                submit={this.handleSubmit}
                salesReturn={this.props.salesReturn}
                // Return Reason
                returnReason={this.props.returnReason}
                visibleAddReturnReason={this.state.visibleAddReturnReason}
                handleModalAddReturnReason={this.handleModalAddReturnReason}
                handleSubmitReturnReason={this.handleSubmitReturnReason}
              />
            </TabPane>
            <TabPane
              tab={<IntlMessages id="jobCostings.tab.attachment" />}
              key="2"
              disabled
            />
          </Tabs>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    salesReturn: state.salesReturn,
    returnReason: state.returnReason
  };
};

export default compose(
  connect(mapStateToProps, { ...actions, storeReturnReason })
)(CreateSalesReturn);
